
/* eslint-disable */
import { defineComponent } from 'vue';
import { useDark, useToggle } from '@vueuse/core';
import { ref } from 'vue';
import { useIntersectionObserver } from '@vueuse/core';

const isDark = useDark();
const toggleDark = useToggle(isDark);

export default defineComponent({
  name: 'App',
  data() {
    return {
      ghlink: 'https://github.com/jsteve22',  
      lilink: 'https://www.linkedin.com/in/jeremy-stevens-14697a206/',
      linkClass: 'text-gray-500 font-bold text-xl hover:text-opacity-40 dark:text-gray-300 dark:hover:text-blue-200',
      arrow_svg: require('@/assets/arrow.svg'),
    }
  },
  methods: {
    toggleDark: toggleDark,
  },
  mounted() {
    document.body.classList.add('dark:bg-slate-700');
  },
  unmounted() {
    document.body.classList.remove('dark:bg-slate-700');
  },
  setup() {
    const target = ref(null);
    const targetIsVisible = ref(false);
    const targetSeenOnce = ref(false);

    const { stop } = useIntersectionObserver(
      target, 
      ([{ isIntersecting }], observerElement:any) => {
        targetIsVisible.value = isIntersecting;
        targetSeenOnce.value = !isIntersecting || targetSeenOnce.value;
      }
    );
    return {
      target, 
      targetIsVisible,
      targetSeenOnce,
    }
  }
});
